<template>
	<div class="potato">
		potato
		<p @click="getParams" data-id="555">{{btn_name}}</p>
		<p class="red">{{ text }}</p>
	</div>
</template>

<script>
	export default{
		name:'potato',
		data(){
			return{
				text:' ',
				btn_name:'点我获取数据'
			}
		},
		methods:{
			 getParams (e) {
			 	console.log(e.target.dataset.id)
		        // 取到路由带过来的参数 
		        let routerParams = this.$route.params.name
		        // 将数据放在当前组件的数据内
		        this.text = routerParams
		        
		        this.btn_name = "已成功获取数据"
		      }
		}
	}
</script>

<style>
.potato{
	font-size: 20px;
	text-align: center;
	color: #2C3E50;
}
</style>


