<template>
	<div id="first_app">
		<p>{{message}}</p>
		<ul>
			<li>
			    <router-link :to="{name:'tomato',params:{user:tet1}}" class="link">番茄</router-link>
		  	</li>
		  	<li>
			    <router-link :to="{name:'potato',params:{name:tet2}}" class="link">土豆</router-link>
		  	</li>
		</ul>
	</div>
</template>

<script>
export default {
  data () {
    return {
     message :'我是列表页',
     tet1:'我是tomato',
     tet2:'我是potato',
    }
  }
}
</script>

<style>
	#first_app{
		color: red;
		font-size: 18px;
	}
</style>


