import Vue from 'vue'
import Router from 'vue-router'
import hello from '@/components/hello'
import first from '@/components/first'  //需要跳转的页面 给组件重新命名
import tomato from '@/components/tomato'
import potato from '@/components/potato'

Vue.use(Router)

export default new Router({
  routes: [
   	{	path: '/', redirect: '/hello'	},  //设置默认路由，redirect将进入时的路径，重新定位到hello上
    {	path: '/hello',name: 'hello',  component: hello	},
    { path: '/first',name: 'firstcomponent', component: first },
    { path: '/tomato:user',name: 'tomato', component: tomato },//带参写法，user不用与router-link处的参数名一致
    { path: '/potato:user',name: 'potato', component: potato },
  ]
})



